import React, { useEffect } from 'react';
import './contact.scss';
import map from '../assets/images/map.png';
import { Formik, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';

const validationSchema = Yup.object({
    fullName: Yup.string().required('Full Name is required'),
    phoneNo: Yup.number().typeError('Phone No. must be a number').required('Phone No. is required'),
    email: Yup.string().email('Invalid email address').required('Email is required'),
    street: Yup.string().required('Street is required'),
    city: Yup.string().required('City is required'),
    postcode: Yup.string().required('Postcode is required'),
    message: Yup.string().required('Message is required')
});

let initialState = {
    fullName: '',
    phoneNo: '',
    email: '',
    street: '',
    city: '',
    postcode: '',
    message: ''
}

const Contact = () => {

    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    }, [])

    const onSubmit = async (values) => {
        let { fullName, phoneNo, email, street, city, postcode, message } = values
        const payload = {
            fullName: fullName,
            contact: phoneNo,
            email: email,
            fullAddress: `${street},${city}-${postcode}`,
            message: message
        }

        let response = await axios.post('http://localhost:8080/api/saveUserQuotes', payload)
        console.log("🚀 ~ onSubmit ~ response:", response)
        console.log("🚀 ~ onSubmit ~ payload:", payload)

    }

    return (
        <div id='ContactUS'>
            <div className="max-w-[96rem] pt-[80px] relative bg-white z-10 m-auto w-full">
                <div className="m-auto w-[90%]">
                    <div id="contact-us" className="relative flex flex-col justify-center py-[55px] md:px-[80px] xl:flex-row  xl:gap-x-11">
                        <div className="relative w-full px-8 pb-8 mx-auto overflow-hidden bg-white xl:w-2/5">
                            <h2 className="text-3xl font-semibold text-gray-800">Get In <span className='text-primary'>Touch</span></h2>
                            <p className="mt-4 text-sm leading-relaxed text-gray-500">Have a specific inquiry or looking to explore new opportunities? Our
                                experienced team is ready to engage with you.</p>

                            <Formik
                                initialValues={initialState}
                                validationSchema={validationSchema}
                                validateOnChange
                                validateOnBlur
                                onSubmit={(values, { resetForm }) => {
                                    onSubmit(values);
                                    resetForm();
                                }}>
                                {({ values, handleChange, handleBlur, setFieldValue }) => (
                                    <Form>
                                        <div className="mt-8 space-y-4">
                                            <div>
                                                <input
                                                    type="text"
                                                    name="fullName"
                                                    placeholder="Full Name"
                                                    value={values.fullName}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    className="px-2 py-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                />
                                                <ErrorMessage name="fullName" component="div" className="text-xs text-red-500" />
                                            </div>

                                            <div>
                                                <input
                                                    type="number"
                                                    name="phoneNo"
                                                    placeholder="Phone No."
                                                    value={values.phoneNo}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    className="px-2 py-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                />
                                                <ErrorMessage name="phoneNo" component="div" className="text-xs text-red-500" />
                                            </div>

                                            <div>
                                                <input
                                                    type="email"
                                                    name="email"
                                                    placeholder="Email"
                                                    value={values.email}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    className="px-2 py-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                />
                                                <ErrorMessage name="email" component="div" className="text-xs text-red-500" />
                                            </div>

                                            <div>
                                                <input
                                                    type="text"
                                                    name="street"
                                                    placeholder="Street"
                                                    value={values.street}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    className="px-2 py-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                />
                                                <ErrorMessage name="street" component="div" className="text-xs text-red-500" />
                                            </div>

                                            <div className="grid grid-cols-2 gap-6">
                                                <div>
                                                    <input
                                                        type="text"
                                                        name="city"
                                                        placeholder="City"
                                                        value={values.city}
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        className="px-2 py-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                    />
                                                    <ErrorMessage name="city" component="div" className="text-xs text-red-500" />
                                                </div>

                                                <div>
                                                    <input
                                                        type="text"
                                                        name="postcode"
                                                        placeholder="Postcode"
                                                        value={values.postcode}
                                                        onChange={handleChange}
                                                        onBlur={handleBlur}
                                                        className="px-2 py-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                    />
                                                    <ErrorMessage name="postcode" component="div" className="text-xs text-red-500" />
                                                </div>
                                            </div>

                                            <div>
                                                <textarea
                                                    as="textarea"
                                                    name="message"
                                                    placeholder="Write Message"
                                                    value={values.message}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    className="px-2 pt-3 bg-white w-full text-gray-800 text-sm border-b border-gray-300 focus:border-[#3498db] outline-none"
                                                />
                                                <ErrorMessage name="message" component="div" className="text-xs text-red-500" />
                                            </div>
                                        </div>

                                        <button
                                            type="submit"
                                            // disabled={isSubmitting}
                                            className="mt-8 flex items-center justify-center text-sm w-full rounded-md px-6 py-3 bg-[#3498db] hover:bg-sky-600 text-white"
                                        >
                                            Send Message
                                        </button>
                                    </Form>
                                )}
                            </Formik>
                        </div>
                        <div className="hidden xl:block">
                            <div className="flex flex-1">
                                <div className="flex flex-col overflow-hidden">
                                    <div className="hidden xl:block">
                                        <img src={map} className='map' alt="World map" />
                                    </div>
                                    <p className="mt-[60px] text-center font-medium text-primary">
                                        Constantsys Technologies</p>
                                    <p className="text-center font-light text-[#111111]">
                                        Based in India&nbsp;<img src="data:image/svg+xml;base64," className="inline" alt="heart icon" />
                                        &nbsp; working worldwide</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Contact