import React from 'react'

const Megamenu = () => {


    const technologies = [
        {
            title: "Web Development",
            items: [
                "MEAN Stack Development",
                "ReactJS Development",
                "NodeJS Development",
                "Python Development",
                "AngularJS Development",
                "PWA - Progressive Web App",
                "Laravel Development",
                "NopCommerce Development"
            ]
        },
        {
            title: "Emerging Technologies",
            items: [
                "Cloud Computing",
                "Robotics Process Automations",
                "IoT Development",
                "Blockchain Development",
                "Chatbot Development"
            ]
        }
    ]

    return (

        <div className="absolute z-50 hidden grid-cols-2 p-4 text-sm w-max -left-52 group-hover:grid md:grid-cols-1">
            <div className="p-8 mx-auto bg-white border border-gray-100 rounded-lg shadow-md">
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2">
                    {technologies?.map(({ title, items }, i) => {
                        if (title === 'Web Development') {
                            return (
                                <div key={i} className='border-r-2 pr-6 border-[rgb(232,231,231)]'>
                                    <h2 className="text-lg font-medium border-b-2 border-[#3498db] pb-2 mb-4">{title}</h2>
                                    <ul className="space-y-2">
                                        {items.map((item, idx) => (
                                            <li key={idx} className="flex m-0 items-center gap-1 text-gray-700 hover:text-[#3498db] transition-colors duration-300 ease-in-out">
                                                <span className='text-[#3498db] text-xl'> • </span>  {item}
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            );
                        }
                    })}

                    {technologies?.map(({ title, items }, i) => {
                        if (title === 'Emerging Technologies') {
                            return (
                                <div key={i} className='pl-6'>
                                    <h2 className="text-lg font-medium border-b-2 border-[#3498db] pb-2 mb-4">{title}</h2>
                                    <ul className="space-y-2">
                                        {items.map((item, idx) => (
                                            <li key={idx} className="flex m-0 items-center gap-1 text-gray-700 hover:text-[#3498db] transition-colors duration-300 ease-in-out">
                                                <span className='text-[#3498db] text-xl'> • </span>  {item}
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            );
                        }
                    })}
                </div>
                <hr className='my-6' />
                <div className="flex flex-col items-center justify-between space-y-4 sm:flex-row sm:space-y-0">
                    <span className="flex items-center gap-3 text-gray-600"><i className="text-2xl text-red-500 bi bi-envelope"></i> support@constantsys.com</span>
                    <span className="flex items-center gap-3 text-gray-600"><i className="text-2xl text-green-600 bi bi-telephone"></i> +91 77788 50746 </span>
                </div>
            </div>
        </div>
    )
}

export default Megamenu