import React, { useEffect, useState } from "react";
import logo from '../../assets/images/logo.png';
import './header.scss';
import { Link, useLocation } from "react-router-dom";
import Megamenu from "../Mega-menu/Megamenu";

export default function Header() {

  const location = useLocation();
  const [bgColor, setBgColor] = useState('none');
  const [shadow, setShadow] = useState('');
  const [currentLocation, setLocation] = useState();
  useEffect(() => {
    setLocation(location.pathname);
  }, [location]);

  const handleScroll = () => {
    if (window.scrollY > 40) {
      setBgColor('#f7f5fd');
      setShadow('shadow-md') // Change to the color you want when scrolled down
    } else {
      setBgColor('none');
      setShadow(''); // Reset to original color when scrolled up
    }
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll); // Clean up event listener on component unmount
    };
  }, []);

  const sideBarToggle = () => {
    let nav = document.getElementById('mobile-nav');
    if (nav != null) {
      if (nav.classList.contains('open')) {
        nav.classList.remove('open');
      } else {
        nav.classList.add('open');
      }
    }
  }
  return (
    <div className="w-full">
      <nav
        className={`sticky top-0 ${shadow} z-30 block items-start duration-100 ease-in-out`}
        style={{
          background: bgColor
        }}

      >
        <div className="w-full max-w-full">
          <div className="m-auto w-[90%]">
            <div className="flex flex-col-reverse md:flex-row">
              <div className="flex w-full items-center justify-between py-[20px]">
                <Link className="" to="/">
                  <img
                    className="h-[25px] lg:h-[30px]"
                    src={logo}
                    alt="Constantsys Logo"
                  />
                </Link>
                <div className="items-center justify-between mobile-view lg:flex" id="mobile-nav">
                  <i className="text-white bi bi-x icon-x" onClick={() => sideBarToggle()}></i>
                  <div className="flex w-full items-center gap-[10px] lg:gap-[40px] text-black-400">
                    <Link className={`shrink-0 font-medium text-primary hover:text-[#3498db] transition-colors duration-300 ease-in-out ${currentLocation == '/' ? 'active' : ''}`} to="/" onClick={() => sideBarToggle()}>
                      {currentLocation == '/' && <i className="bi bi-dot dot-icon"></i>}Home
                    </Link>
                    <a className="relative m-0 group">
                      <span
                        className={`shrink-0 font-medium text-primary hover:text-[#3498db] transition-colors duration-300 ease-in-out ${currentLocation == '/technology' ? 'active' : ''}`}
                        onClick={() => sideBarToggle()}
                      >
                        {currentLocation == '/technology' && <i className="bi bi-dot dot-icon"></i>}
                        Technology
                      </span>
                      <Megamenu />
                    </a>
                    <Link className={`shrink-0 font-medium text-primary hover:text-[#3498db] transition-colors duration-300 ease-in-out ${currentLocation == '/about-us' ? 'active' : ''}`} to="/about-us" onClick={() => sideBarToggle()}>
                      {currentLocation == '/about-us' && <i className="bi bi-dot dot-icon"></i>}
                      Get to Know US
                    </Link>
                    <Link className={`shrink-0 font-medium text-primary hover:text-[#3498db] transition-colors duration-300 ease-in-out ${currentLocation == '/portfolio' ? 'active' : ''}`} to="/portfolio" onClick={() => sideBarToggle()}>
                      {currentLocation == '/portfolio' && <i className="bi bi-dot dot-icon"></i>}
                      Portfolio
                    </Link>
                    <Link className={`shrink-0 font-medium text-primary hover:text-[#3498db] transition-colors duration-300 ease-in-out ${currentLocation == '/join-us' ? 'active' : ''}`} to="/join-us" onClick={() => sideBarToggle()}>
                      {currentLocation == '/join-us' && <i className="bi bi-dot dot-icon"></i>}
                      Join Us
                    </Link>
                    <Link className={`shrink-0 font-medium text-primary hover:text-[#3498db] transition-colors duration-300 ease-in-out ${currentLocation == '/contact-us' ? 'active' : ''}`} to="/contact-us" onClick={() => sideBarToggle()}>
                      {currentLocation == '/contact-us' && <i className="bi bi-dot dot-icon"></i>}
                      Contact Us
                    </Link>
                  </div>
                </div>
                <div className="flex h-[43px] w-[43px] cursor-pointer items-center justify-center lg:hidden">
                  <button
                    type="button"
                    data-drawer-target="drawer-example"
                    data-drawer-show="drawer-example"
                    aria-controls="drawer-example"
                    onClick={() => sideBarToggle()}
                  >
                    <img
                      alt="ICON"
                      src="data:image/svg+xml;base64,"
                      height={43}
                      width={43}
                      style={{ filter: 'hue-rotate(68deg)' }}
                    />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}
